// src/components/Header.jsx
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import '../styles/header.css';
import NameSearchBar from './NameSearchBar'; // Asegúrate de que el nombre coincida

const Header = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const navigate = useNavigate();

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const handleSearchByName = async (name) => {
    try {
        const response = await fetch(`http://localhost:5000/api/influencers/search?q=${encodeURIComponent(name)}`);
        if (response.status === 404) {
            alert('No se encontró a la chica con ese nombre.');
            return;
        }

        const data = await response.json();
        if (data && data.id) {
            navigate(`/perfil/${data.id}`);
        } else {
            alert('No se encontró a la chica con ese nombre.');
        }
    } catch (error) {
        console.error("Error fetching influencer by name:", error);
    }
  };

  return (
    <header className="header-container">
      <div className="logo">
        <Link to="/" className="main-title"><span>Promoción</span> Chicas</Link>
      </div>
      <button className="mobile-menu-btn" onClick={toggleMobileMenu} aria-label="Toggle navigation menu">
        &#9776;
      </button>
      <nav className={`nav-links ${isMobileMenuOpen ? 'active' : ''}`}>
        <ul>
          <li><Link to="/influencers">Influencers</Link></li>
          <li><Link to="/about">Acerca de</Link></li>
          <li><Link to="/contact">Contacto</Link></li>
        </ul>
        {/* Utiliza el componente NameSearchBar que recibe la función onSearchByName */}
        <NameSearchBar
          onSearchByName={handleSearchByName}
        />
      </nav>
    </header>
  );
};

export default Header;
